<template>
  <v-app-bar
    app
    dark
    dense
    color="purple darken-4"
    elevate-on-scroll
    height="60em"
  >
    <v-app-bar-nav-icon class="draw" @click="drawer = !drawer"
      ><v-icon>mdi-transfer-right</v-icon></v-app-bar-nav-icon
    >

    <v-toolbar-title
      ><span style="cursor: pointer" @click="$router.push('/home')">Home</span>
      / {{ $route.meta.title }}
    </v-toolbar-title>

    <v-spacer></v-spacer>

    <v-btn
      v-if="!currentUser && $route.meta.title != 'เข้าสู่ระบบ'"
      small
      color="blue"
      id="login"
      to="/login"
      link
    >
      <v-icon left>mdi-login</v-icon>เข้าสู่ระบบ
    </v-btn>

    <v-menu
      open-on-hover
      left
      offset-y
      rounded="b-xl t-lg"
      v-if="currentUser"
      style="z-index:11;"
    >
      <template v-slot:activator="{ on, attrs }">
        <v-btn color="blue-grey" v-bind="attrs" v-on="on" small fab>
          <v-avatar size="30" v-bind="attrs" v-on="on">
            <img
              src="../../assets/avatar (1).png"
              alt="alt"
              v-if="currentUser && showAdminBoard"
            />
            <img
              src="../../assets/avatar2.png"
              alt="alt"
              v-if="currentUser && !showAdminBoard"
            />
          </v-avatar>
        </v-btn>
      </template>
      <v-list class="text-center pa-2">
        <v-avatar size="44">
          <img
            src="../../assets/avatar (1).png"
            alt="alt"
            v-if="currentUser && showAdminBoard"
          />
          <img
            src="../../assets/avatar2.png"
            alt="alt"
            v-if="currentUser && !showAdminBoard"
          />
        </v-avatar>
        <v-list-item-title class="purple--text"
          >@{{ currentUser.username }}</v-list-item-title
        >
        <v-list-item-title class="font-weight-light">{{
          currentUser.email
        }}</v-list-item-title>

        <v-list-item-title class="font-weight-light"
          ><v-divider class="mx-4"></v-divider
          ><v-btn
            text
            class="logout mt-2"
            color="cyan"
            @click="getData(currentUser.id)"
            ><v-icon>mdi-circle-edit-outline</v-icon> แก้ไขข้อมูลส่วนตัว</v-btn
          ></v-list-item-title
        >
        <v-btn text small color="red" dark class="logout" @click="logOut">
          <v-icon>mdi-logout</v-icon>
          ออกจากระบบ
        </v-btn>
      </v-list>
    </v-menu>
    <!-- //edit  -->
    <v-dialog v-model="dialog" max-width="320px">
      <v-card>
        <v-card-title>
          <v-row class="justify-between">
            <v-btn fab color="warning"
              ><v-icon>mdi-circle-edit-outline</v-icon></v-btn
            >
            <p class="ma-3">แก้ไขข้อมูลส่วนตัว</p>
          </v-row>
          <!-- <span class="headline">{{ formTitle }}</span> -->
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12">
                <v-col ref="form" lazy-validation>
                  <v-text-field
                    v-model="item.username"
                    label="ชื่อผู้ใช้งาน"
                  ></v-text-field>
                </v-col>
                <v-col>
                  <v-text-field
                    v-model="item.email"
                    label="อีเมล"
                  ></v-text-field>
                </v-col>
                <v-row>
                  <v-col class="ml-4">
                    <v-text-field
                      v-model="item.firstname"
                      type="text"
                      label="ชื่อ"
                    ></v-text-field>
                  </v-col>
                  <v-col class="mr-2">
                    <v-text-field
                      v-model="item.lastname"
                      type="text"
                      label="นามสกุล"
                    ></v-text-field>
                  </v-col>
                </v-row>
                <v-row class="pl-3 pr-3">
                  <v-col>
                    <v-text-field
                      type="password"
                      v-model="password"
                      label="รหัสผ่านเดิม"
                    ></v-text-field>
                    <!-- <code v-show="item.password !== ''">{{ item.password }}</code> -->
                  </v-col>
                  <v-col>
                    <v-text-field
                      type="password"
                      v-model="password"
                      label="รหัสผ่านใหม่"
                    ></v-text-field>
                    <!-- <code v-show="item.password !== ''">{{ item.password }}</code> -->
                  </v-col>
                </v-row>
              </v-col>
              <v-row>
                <v-list-item v-if="error">
                  <v-icon color="error" text left
                    >mdi-information-outline</v-icon
                  >
                  <v-list-item-title class="red--text">
                    {{ error }}
                  </v-list-item-title>
                </v-list-item>
              </v-row>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="error" text @click="close">
            ยกเลิก
          </v-btn>
          <v-btn color="blue darken-1" text @click="save(currentUser.id)">
            บันทึก
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-snackbar v-model="snackbar" :timeout="timeout" :color="colors">
      {{ text }}

      <template v-slot:action="{ on, attrs }">
        <v-icon
          small
          color="white"
          text
          v-bind="attrs"
          @click="snackbar = false"
          v-on="on"
        >
          mdi-close
        </v-icon>
      </template>
    </v-snackbar>
  </v-app-bar>
</template>
<script>
import TutorialDataService from "../../services/TutorialDataService";
export default {
  data() {
    return {
      snackbar: false,
      item: "",
      colors: "",
      text: "",
      error: false,
      password: "",
      defaultItem: {
        id: "",
        username: "",
        email: "",
        password: "",
        createdAt: "",
      },
      drawer: true,
      mini: true,
      dialog: false,
      model: 1,
      timeout: 2000,
    };
  },
  methods: {
    logOut() {
      this.$store.dispatch("auth/logout");
      this.$router.push("/home");
    },
    save(_id) {
      console.log(_id);
      TutorialDataService.update(this.item.id, this.item)
        .then(
          (response) => {
            console.log(response.data);
            this.snackbar = true;
            this.text = "แก้ไขข้อมูลเรียบร้อย";
            this.colors = "success";
            this.close();
          },
          (error) => {
            this.error =
              (error.response &&
                error.response.data &&
                error.response.data.message) ||
              error.message ||
              error.toString();
          }
        )

        // .then(() => {
        //   this.password = "";
        //   this.dialog = false;
        // })
        // .then(this.close())
        .catch((e) => {
          console.log(e);
        });
    },
    getData(id) {
      TutorialDataService.get(id)
        .then((response) => {
          this.item = response.data;
          this.dialog = true;
          console.log(response.data);
        })
        .catch((e) => {
          console.log(e);
        });
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.item = Object.assign({}, this.defaultItem);
      });
    },
  },
  computed: {
    currentUser() {
      return this.$store.state.auth.user;
    },
    showAdminBoard() {
      if (this.currentUser && this.currentUser.roles) {
        return this.currentUser.roles.includes("ROLE_ADMIN");
      }
      return false;
    },
    showModeratorBoard() {
      if (this.currentUser && this.currentUser.roles) {
        return this.currentUser.roles.includes("ROLE_MODERATOR");
      }
      return false;
    },
  },
};
</script>
<style scoped>
#pt {
  color: #000 !important;
}
@media only screen and (min-width: 600px) {
  .draw {
    display: none !important;
  }
}
@media only screen and (max-width: 600px) {
  #login span {
    display: none;
  }
}
.draw {
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  max-width: 2rem;
  max-height: 2rem;
  margin-right: 8px;
}

* {
  font-size: 15px !important;
}
.logout {
  font-size: 10px !important;
}
</style>
